<template>
  <div class="allScenario">
    <div class="main">
      <div class="middle">
        <div class="left">
          <Device v-if="show === '1'"></Device>
          <Entity v-if="show === '2'"></Entity>
          <Automation v-if="show === '3'"></Automation>
        </div>
        <div class="right">
          <div class="name">
            <el-form>
              <el-form-item :model="form">
                <el-input v-model="projectName" ref="input"></el-input>
              </el-form-item>
            </el-form>
            <i class="icon iconfont icon-baocun" @click="saveProjectName"></i>
          </div>

          <div class="image" @dragover="dragOver" @drop="drop($event)">
            <el-empty
              description="Please select a scenario"
              v-show="currentScenario == ''"
            ></el-empty>
            <el-image
              :src="bigImage"
              fit="fill"
              v-show="currentScenario != ''"
            ></el-image>
            <!-- icon -->
            <div
              v-for="item in add_icons"
              :key="item.id"
              :style="{
                position: 'absolute',
                left: item.left,
                top: item.top,
                height: '36px',
                width: '36px',
                fontSize: '30px',
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
                borderRadius: '50%',
                backgroundColor: 'rgba(224, 150, 58,0.95)',
                boxShadow: '0px 0px 8px 3px rgba(224, 150, 58, 0.5)',
              }"
              @dragstart="dragStart(item)"
            >
              <span
                :class="item.icon"
                v-show="!item.show_img"
                :draggable="true"
              />
              <img
                :src="item.img"
                v-show="item.show_img"
                style="height: 100%; width: 100%"
                :draggable="true"
              />
            </div>
          </div>

          <div class="scroll">
            <i
              class="el-icon-arrow-left"
              @click="leftMove"
              style="cursor: pointer"
            ></i>

            <ul>
              <li class="addPic" v-show="n == 0" @click="get_render">
                <div>
                  <i class="el-icon-plus"></i>
                  <el-button round>Add pictures</el-button>
                </div>
              </li>

              <li
                v-for="item in showPic"
                :key="item.id"
                @mouseleave="conceal(item)"
              >
                <el-image
                  :src="scenarios[item].pic"
                  fit="fill"
                  @click="changImage(scenarios[item].pic, item)"
                ></el-image>
                <i class="el-icon-more more" @mouseenter="present(item)"></i>
                <div class="editScenario" v-show="scenarios[item].show">
                  <div>
                    <img
                      src="../../assets/pic/default.png"
                      @click="setBg(item)"
                    />
                    <span>Background</span>
                  </div>
                  <div>
                    <img
                      src="../../assets/pic/delete.png"
                      @click="deleteScenario(item)"
                    />
                    <span>Delete</span>
                  </div>
                  <div>
                    <img
                      src="../../assets/pic/copy.png"
                      @click="copyIconStyle(item)"
                    />
                    <span>Copy style</span>
                  </div>
                  <div>
                    <img
                      src="../../assets/pic/paste.png"
                      @click="pasteIconStyle(item)"
                    />
                    <span>Paste style</span>
                  </div>
                </div>
                <div class="ScenarioName">
                  <el-form>
                    <el-form-item :model="form">
                      <el-input
                        v-model="scenarios[item].name"
                        ref="input"
                      ></el-input>
                    </el-form-item>
                  </el-form>
                  <i
                    class="icon iconfont icon-baocun"
                    @click="saveScenarioName(item, scenarios[item].name)"
                  ></i>
                </div>
              </li>
            </ul>
            <i
              class="el-icon-arrow-right"
              @click="rightMove"
              style="cursor: pointer"
            ></i>
          </div>
        </div>
        <el-dialog
          title="Gallery"
          :visible.sync="addScenarioDialogFormVisible"
          class="scenarioDialog"
          width="1100px"
          :modal-append-to-body="false"
        >
          <ul class="demo-image">
            <li class="uploadLi">
              <el-upload
                action="#"
                :auto-upload="false"
                :show-file-list="false"
                :http-request="handelHttp"
                :on-change="handelChange"
                multiple
                :limit="limitPic"
                :on-exceed="handleExceed"
                list-type="picture-card"
                accept="JPG,.PNG,.JPEG,.jpg,.png,.jpeg"
                class="upload"
              >
                <i class="el-icon-plus"></i>
              </el-upload>
            </li>
            <li v-for="(item, index) in render_pic" :key="index">
              <el-checkbox v-model="item.check">
                <el-image :src="item.pic" fit="fill"> </el-image>
              </el-checkbox>
            </li>
          </ul>
          <el-button type="primary" @click="scenarioDialogConfirm()" round
            >Confirm</el-button
          >
        </el-dialog>
      </div>
    </div>
  </div>
</template>

<script>
import throttle from "@/utils/throttle";

import Device from "./components/Device.vue";
import Entity from "./components/Entity.vue";
import Automation from "./components/Automation.vue";

import { mapState } from "vuex";
export default {
  components: {
    Device,
    Entity,
    Automation,
  },
  data() {
    return {
      visitor: null,
      addScenarioDialogFormVisible: false,
      token: window.sessionStorage.getItem("ha_token"),
      ha_url: window.sessionStorage.getItem("hass_url"),
      form: {},
      projects: "",
      scenarios: "",
      // Currently project name
      projectName: "",
      // Control page flipping left and right
      n: 0,
      left: 0,
      right: 2,
      // List of scenario names
      scenarioList: [],
      // Currently displayed pic
      bigImage: "",
      // Currently selected scenario name
      currentScenario: "",
      // Currently displayed icon
      add_icons: {},
      render_pic: [],
      planId: "",
      // Copied icon
      copyStyle: {},
      instance: "",
      // Limit the number of locally uploaded images
      limitPic: 10,
    };
  },
  created() {
    this.initOp();
  },
  methods: {
    initOp() {
      this.$store.commit("setAutoState", true);
      this.$store.commit("setScenarioState", false);
      this.visitor = JSON.parse(JSON.stringify(this.$store.state.visitor));
      this.$store.commit("setStep", "2");
      this.planId = this.$store.state.planId;
      this.form.name = this.$store.state.projectName;
      this.projects = JSON.parse(this.$store.state.projects);
      this.projectName = this.$store.state.projectName;
      this.scenarios = JSON.parse(
        JSON.stringify(this.projects[this.projectName].scenarios)
      );
      this.scenarioList = this.projects[this.projectName].scenarioList;
      this.bigImage = this.projects[this.projectName].default;
      this.instance = this.$store.state.instance;
    },
    handleExceed(files, fileList) {
      this.$message.warning(
        `The current limit is to select ${this.limitPic} images, but you have selected ${files.length} images this time.`
      );
    },
    async handelChange(file, fileList) {
      let base64Str;
      try {
        base64Str = await this.getBase64(file.raw);
      } catch {
        console.log("err1");
      }
      let uid = new Date().getTime() + Math.floor(Math.random() * 10000000);
      await this.init_automation(uid);
      this.projects[this.projectName].scenarioList.push(uid);
      this.projects[this.projectName].scenarios[uid] = {
        name: uid,
        pic: base64Str,
        add_pic: {},
        add_icons: {},
        automation_id: "",
        automation_name: "",
        show: false,
      };
      this.projects = JSON.parse(JSON.stringify(this.projects));
      this.scenarioList = this.projects[this.projectName].scenarioList;
      this.scenarios = JSON.parse(
        JSON.stringify(this.projects[this.projectName].scenarios)
      );
      console.log(this.projects);
      this.left = 0;
      this.right = 2;
      this.$store.commit("setProjects", JSON.stringify(this.projects));
      this.addScenarioDialogFormVisible = false;
      this.$message({
        showClose: false,
        message: "Successfully added",
        type: "success",
        center: true,
        duration: 3000,
      });
    },
    update() {
      this.$store.commit("setProjects", JSON.stringify(this.projects));
      this.scenarios = JSON.parse(
        JSON.stringify(this.projects[this.projectName].scenarios)
      );
      this.scenarioList = JSON.parse(
        JSON.stringify(this.projects[this.projectName].scenarioList)
      );
      this.$store.dispatch("saveConfig", this);
    },
    conceal(scenarioName) {
      if (this.scenarios[scenarioName].show == true) {
        this.scenarios[scenarioName].show = false;
        this.scenarios = JSON.parse(JSON.stringify(this.scenarios));
        this.projects = JSON.parse(this.$store.state.projects);
        this.update();
      }
    },
    present(scenarioName) {
      this.scenarios[scenarioName].show = true;
      this.scenarios = JSON.parse(JSON.stringify(this.scenarios));
    },
    pasteIconStyle(scenarioName) {
      if (Object.keys(this.copyStyle).length == 0) {
        this.$message({
          type: "error",
          message: "No icon has been copied!",
        });
        return;
      }
      this.projects[this.projectName].scenarios[scenarioName].add_icons =
        JSON.parse(JSON.stringify(this.copyStyle));
      this.$store.commit("setProjects", JSON.stringify(this.projects));
      this.add_icons =
        this.projects[this.projectName].scenarios[scenarioName].add_icons;
      this.$message({
        type: "success",
        message: "Icon pasted successfully!",
      });
    },
    copyIconStyle(scenarioName) {
      this.copyStyle = JSON.parse(
        JSON.stringify(
          this.projects[this.projectName].scenarios[scenarioName].add_icons
        )
      );
      this.$message({
        type: "success",
        message: "Icon copied successfully!",
      });
    },
    setBg(scenarioName) {
      this.$confirm(
        "Do you want to set this scenario as the default?",
        "Prompt",
        {
          confirmButtonText: "Confirm",
          cancelButtonText: "Cancel",
          type: "warning",
        }
      )
        .then(() => {
          this.bigImage = this.scenarios[scenarioName].pic;
          this.projects[this.projectName].default = this.bigImage;
          this.projects[this.projectName].defaultScenario = scenarioName;
          for (let i = 0; i < this.scenarioList.length; i++) {
            if (this.scenarioList[i] == scenarioName) {
              this.scenarioList.splice(i, 1);
              break;
            }
          }
          this.scenarioList.unshift(scenarioName);
          this.projects[this.projectName].scenarioList = this.scenarioList;
          this.$store.commit("setProjects", JSON.stringify(this.projects));
          this.projects = JSON.parse(this.$store.state.projects);
          this.scenarioList = this.projects[this.projectName].scenarioList;
          this.$message({
            type: "success",
            message: "Set successfully!",
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "Setting canceled",
          });
        });
    },
    deleteScenario(scenarioName) {
      this.$confirm("Do you want to delete this scenario?", "Prompt", {
        confirmButtonText: "Confirm",
        cancelButtonText: "Cancel",
        type: "warning",
      })
        .then(() => {
          if (this.projects[this.projectName].defaultScenario == scenarioName) {
            this.$message({
              type: "error",
              message: "The background image cannot be deleted!",
            });
            return;
          }
          if (this.currentScenario == scenarioName) {
            this.$store.commit("setShow", "1");
          }
          //delete automation
          this.instance.delete(
            "/api-ha/api/config/automation/config/" +
              this.projects[this.projectName].scenarios[scenarioName]
                .automation_id
          );

          for (let i = 0; i < this.scenarioList.length; i++) {
            if (this.scenarioList[i] == scenarioName) {
              this.scenarioList.splice(i, 1);
              break;
            }
          }
          this.left = 0;
          this.right = 2;
          this.projects[this.projectName].scenarioList = this.scenarioList;
          delete this.scenarios[scenarioName];
          this.projects[this.projectName].scenarios = this.scenarios;
          this.icons = {};
          this.add_icons = {};
          this.$store.commit("setSelectScenarioName", "");
          this.bigImage = this.projects[this.projectName].default;
          this.update();
          this.scenarioList = this.projects[this.projectName].scenarioList;
          this.$message({
            type: "success",
            message: "Successfully deleted!",
          });
          this.currentScenario = "";
          this.$store.commit("setAutoState", true);
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "Deletion canceled",
          });
        });
    },
    validate(str) {
      const regex = /^[a-zA-Z0-9_]+$/;
      return regex.test(str);
    },
    saveScenarioName(oldName, newName) {
      this.projects = JSON.parse(this.$store.state.projects);
      if (this.scenarioList.includes(this.scenarios[oldName].name)) {
        // Not allowed to duplicate
        this.$message({
          showClose: false,
          message: "Duplicate scenario name",
          type: "error",
          center: true,
          duration: 1000,
        });
        this.scenarios[oldName].name = oldName;
      } else if (!this.validate(newName)) {
        // Limit the range of naming
        this.$message({
          showClose: false,
          message: "The scenario name can only contain letters, numbers  and _",
          type: "error",
          center: true,
          duration: 4000,
        });
        this.scenarios[oldName].name = oldName;
      } else {
        if (oldName == this.projects[this.projectName].defaultScenario) {
          this.projects[this.projectName].defaultScenario = newName;
        }
        let autId =
          this.projects[this.projectName].scenarios[oldName].automation_id;
        if (autId) {
          // Modify automation name
          this.instance
            .get("/api-ha/api/config/automation/config/" + autId)
            .then((res) => {
              let data = res.data;
              let entity_id = data.alias
                .toString()
                .toLowerCase()
                .replaceAll("-", "_");
              let obj = {
                area_id: null,
                entity_id: "automation." + entity_id,
                icon: null,
                id: this.$store.state.wsId,
                name: newName,
                new_entity_id: "automation." + entity_id,
                type: "config/entity_registry/update",
              };
              this.$store.commit("setWsId");
              this.$store.state.webSocket.send(JSON.stringify(obj));
              this.$store.commit("setWsId");
            });
        }
        this.projects[this.projectName].scenarios[newName] = JSON.parse(
          JSON.stringify(this.projects[this.projectName].scenarios[oldName])
        );
        delete this.projects[this.projectName].scenarios[oldName];
        this.projects[this.projectName].scenarios[newName].name = newName;
        for (let i = 0; i < this.scenarioList.length; i++) {
          if (this.scenarioList[i] == oldName) {
            this.scenarioList.splice(i, 1, newName);
            break;
          }
        }
        this.projects[this.projectName].scenarioList = this.scenarioList;
        this.projects = JSON.parse(JSON.stringify(this.projects));
        this.update();
        if (this.$store.state.selectScenarioName == oldName) {
          this.$store.commit("setSelectScenarioName", newName);
        }
        this.$message({
          showClose: false,
          message: "Modification successful",
          type: "success",
          center: true,
          duration: 3000,
        });
      }
    },
    saveProjectName() {
      if (this.projectName in Object.keys(this.projects)) {
        this.$message({
          showClose: false,
          message: "Duplicate project name",
          type: "error",
          center: true,
          duration: 1000,
        });
        this.projectName = this.$store.state.projectName;
      } else {
        this.projects[this.projectName] = JSON.parse(
          JSON.stringify(this.projects[this.$store.state.projectName])
        );
        this.projects[this.projectName].name = this.projectName;
        delete this.projects[this.$store.state.projectName];
        this.projects = JSON.parse(JSON.stringify(this.projects));
        this.$store.commit("setProjectName", this.projectName);
        this.$store.commit("setProjects", JSON.stringify(this.projects));
        this.scenarios = JSON.parse(
          JSON.stringify(this.projects[this.projectName].scenarios)
        );
        this.scenarioList = this.projects[this.projectName].scenarioList;
        this.currentScenario = "";
        this.$store.commit("setAutoState", true);
        this.bigImage = "";
        this.$store.commit("setShow", "1");
        this.add_icons = {};
        this.$message({
          showClose: false,
          message: "Modification successful",
          type: "success",
          center: true,
          duration: 3000,
        });
      }
    },
    init_automation(scenarioName) {
      let uid = new Date().getTime() + Math.floor(Math.random() * 10000000);
      let obj = {
        alias: scenarioName,
        description: "",
        trigger: [],
        condition: [],
        action: [],
        mode: "single",
      };
      this.instance
        .post("/api-ha/api/config/automation/config/" + uid, obj)
        .then((res) => {
          this.projects[this.$store.state.projectName].scenarios[scenarioName][
            "automation_id"
          ] = uid;
          this.projects[this.$store.state.projectName].scenarios[scenarioName][
            "automation_name"
          ] = scenarioName.toString().toLowerCase().replaceAll("-", "_");
          console.log(this.projects);
          this.$store.commit("setProjects", JSON.stringify(this.projects));
        })
        .catch((err) => console.log(err));
    },
    async scenarioDialogConfirm() {
      let uid = new Date().getTime() + Math.floor(Math.random() * 10000000);
      for (let i = 0; i < this.render_pic.length; i++) {
        if (this.render_pic[i].check == true) {
          uid += 1;
          this.projects[this.projectName].scenarioList.push(uid);
          this.projects[this.projectName].scenarios[uid] = {
            name: uid,
            pic: this.render_pic[i].pic,
            add_pic: {},
            add_icons: {},
            show: false,
            automation_id: "",
            automation_name: "",
          };
          await this.init_automation(uid);
        }
      }
      this.projects = JSON.parse(JSON.stringify(this.projects));
      this.scenarioList = this.projects[this.projectName].scenarioList;
      this.scenarios = JSON.parse(
        JSON.stringify(this.projects[this.projectName].scenarios)
      );
      this.left = 0;
      this.right = 2;
      this.$store.commit("setProjects", JSON.stringify(this.projects));
      this.addScenarioDialogFormVisible = false;
      this.$message({
        showClose: false,
        message: "Successfully added",
        type: "success",
        center: true,
        duration: 3000,
      });
    },
    get_render: throttle(function () {
      this.render_pic = [];
      const a = async () => {
        let request_body = {
          obsDesignId: this.planId,
        };
        let res = await this.$axios.get(
          "/api-ks/koolsmart/api/site/design/designrenderpics",
          {
            params: request_body,
          }
        );
        if (res.data.renderPics.length == 0) {
          this.$message.error("No rendered image found");
          return;
        }
        this.addScenarioDialogFormVisible = true;
        for (let i = 0; i < res.data.renderPics.length; i++) {
          let flag = 1;
          for (let j = 0; j < this.render_pic.length; j++) {
            if (res.data.renderPics[i] == this.render_pic[j].pic) {
              flag = 0;
              break;
            }
          }
          if (flag) {
            this.render_pic.push({ pic: res.data.renderPics[i], check: false });
          }
        }
      };
      if (this.$store.state.isVisitor) {
        // visitor mode
        this.addScenarioDialogFormVisible = true;
        for (let i = 0; i < this.visitor.renderPics.length; i++) {
          let flag = 1;
          for (let j = 0; j < this.render_pic.length; j++) {
            if (this.visitor.renderPics[i] == this.render_pic[j].pic) {
              flag = 0;
              break;
            }
          }
          if (flag) {
            this.render_pic.push({
              pic: this.visitor.renderPics[i],
              check: false,
            });
          }
        }
      } else {
        a();
      }
    }),

    dragStart(entity) {
      this.$store.commit("setMoveEntityName", entity.name);
      this.$store.commit("setMoveEntityId", entity.id);
      this.$store.commit("setMoveEntityIcon", entity.icon);
      this.$store.commit("setMoveEntityImg", entity.img);
      this.$store.commit("setMoveEntityShow", entity.show_img);
    },
    drop(e) {
      // Drag the icon
      if (this.currentScenario != "") {
        let outer = document.getElementsByClassName("image")[0];
        let l = outer.getBoundingClientRect().left;
        let t = outer.getBoundingClientRect().top;
        // create icon
        this.add_icons[this.moveEntityId] = {};
        this.add_icons[this.moveEntityId]["id"] = this.moveEntityId;
        this.add_icons[this.moveEntityId]["left"] = e.clientX - l - 15 + "px";
        this.add_icons[this.moveEntityId]["top"] = e.clientY - t - 15 + "px";
        this.add_icons[this.moveEntityId]["show_img"] = this.moveEntityShow;
        this.add_icons[this.moveEntityId]["icon"] = this.moveEntityIcon;
        this.add_icons[this.moveEntityId]["img"] = this.moveEntityImg;

        let left_percent = (
          ((e.clientX - l) / outer.offsetWidth) *
          100
        ).toFixed(2);
        let top_percent = (
          ((e.clientY - t) / outer.offsetHeight) *
          100
        ).toFixed(2);
        if (left_percent < 0) left_percent = 0;
        if (top_percent < 0) top_percent = 0;
        left_percent += "%";
        top_percent += "%";
        this.add_icons[this.moveEntityId]["left_percent"] = left_percent;
        this.add_icons[this.moveEntityId]["top_percent"] = top_percent;
        console.log(this.add_icons[this.moveEntityId]);

        this.add_icons = JSON.parse(JSON.stringify(this.add_icons));
        this.projects[this.projectName].scenarios[this.currentScenario][
          "add_icons"
        ] = this.add_icons;
        this.$store.commit("setProjects", JSON.stringify(this.projects));
      } else
        this.$message({
          showClose: false,
          message: "Please select a scenario first",
          type: "error",
          center: true,
          duration: 3000,
        });
    },
    dragOver(e) {
      e.preventDefault();
    },
    changImage(img, name) {
      this.projects = JSON.parse(this.$store.state.projects);
      this.$store.commit("setSelectScenarioName", name);
      this.bigImage = img;
      this.currentScenario = name;
      this.$store.commit("setAutoState", false);
      this.add_icons =
        this.projects[this.projectName].scenarios[name].add_icons;
    },
    leftMove() {
      if (this.n == 0) {
        this.$message({
          message: "This is already the first page",
          type: "warning",
        });
        return;
      }
      this.n--;
      if (this.n == 0) {
        this.left = 0;
        this.right = 2;
      } else {
        this.left -= 3;
        this.right = this.left + 3;
      }
      this.scenarios = JSON.parse(
        JSON.stringify(this.projects[this.projectName].scenarios)
      );
    },
    rightMove() {
      if (this.scenarioList.length <= this.right) {
        this.$message({
          message: "This is already the last page",
          type: "warning",
        });
        return;
      }
      this.n++;
      this.left = 2 + (this.n - 1) * 3;
      this.right = this.left + 3;
      this.scenarios = JSON.parse(
        JSON.stringify(this.projects[this.projectName].scenarios)
      );
    },
    getBase64(file) {
      return new Promise((resolve, reject) => {
        const reader = new FileReader();
        let imgResult = "";
        reader.readAsDataURL(file);
        reader.onload = function () {
          imgResult = reader.result;
        };
        reader.onerror = function (error) {
          reject(error);
        };
        reader.onloadend = function () {
          console.log(imgResult);
          resolve(imgResult);
        };
      });
    },
  },

  computed: {
    show() {
      return this.$store.state.show;
    },
    showPic() {
      if (this.right > this.scenarioList.length) {
        this.right = this.scenarioList.length;
      }
      return this.scenarioList.slice(this.left, this.right);
    },
    ...mapState([
      "moveEntityId",
      "moveEntityImg",
      "moveEntityIcon",
      "moveEntityShow",
    ]),
  },
  watch: {
    "$store.state.show": function (newVal) {
      console.log(newVal);
      this.show = newVal;
    },
  },
};
</script>

