<template>
  <div class="aut">
    <iframe
      :src="automationUrl"
      scrolling="no"
      height="100%"
      width="100%"
      frameborder="0"
      style="border-radius: 20px"
    ></iframe>
  </div>
</template>

<script>
export default {
  data() {
    return {
      projects: "",
      ha_url: window.sessionStorage.getItem("hass_url"),
      automationUrl: "",
      automationId: "",
      instance: null,
    };
  },
  created() {
    this.$store.commit("setStep", "3");
    this.instance = this.$store.state.instance;
    this.ge_automation();
  },

  methods: {
    init_automation(scenarioName) {
      let uid = new Date().getTime() + Math.floor(Math.random() * 10000000);
      let obj = {
        alias: scenarioName,
        description: "",
        trigger: [],
        condition: [],
        action: [],
        mode: "single",
      };

      this.instance
        .post("/api-ha/api/config/automation/config/" + uid, obj)
        .then((res) => {
          this.projects[this.$store.state.projectName].scenarios[scenarioName][
            "automation_id"
          ] = uid;
          this.projects[this.$store.state.projectName].scenarios[scenarioName][
            "automation_name"
          ] = scenarioName.toString().toLowerCase().replaceAll("-", "_");
          this.$store.commit("setProjects", JSON.stringify(this.projects));
          this.automationUrl = "/api-ha/config/automation/edit/" + uid;
          this.ge_automation();
        })
        .catch((err) => console.log(err));
    },
    // scenario
    ge_automation() {
      // Display automation interface
      this.projects = JSON.parse(this.$store.state.projects);
      this.scenario = this.$store.state.selectScenarioName;
      console.log(
        this.projects[this.$store.state.projectName].scenarios[this.scenario]
          .automation_id
      );
      this.automationId =
        this.projects[this.$store.state.projectName].scenarios[
          this.scenario
        ].automation_id;
      if (this.scenario != "") {
        this.automationUrl =
          "/api-ha/config/automation/edit/" + this.automationId;
        this.instance
          .get(this.automationUrl)
          .then((res) => console.log(res))
          .catch((err) => {
            this.init_automation(this.scenario);
          });
      } else {
        this.automationUrl = "/api-ha/config/automation/edit/new";
      }
    },
  },
  computed: {
    scenario() {
      return this.$store.state.selectScenarioName;
    },
  },
  watch: {
    "$store.state.selectScenarioName": function (newVal) {
      this.scenario = newVal;
      this.ge_automation();
    },
  },
};
</script>

<style lang="less" scoped>
.aut {
  height: 832px;
  width: 100%;
  border-radius: 20px;
  box-shadow: 0px 5px 21px 0px rgba(0, 0, 0, 0.26);
}
</style>
