<template>
    <div class="all">
        <div class="header">
            <i class="el-icon-arrow-left" @click="back"></i>
            <p :title="deviceName">{{ deviceName }}</p>
        </div>
        <div class="footer">
            <ul class="footer_ul">
                <li v-for="item in entities" :key="item.id">
                    <i class="el-icon-picture-outline edit" @click="selectEntity(item.name, item.id)"
                        title="Custom icon"></i>
                    <div class="iconDiv" draggable="true" @dragstart="dragStart($event, item)">
                        <img :src="item.img" v-show="item.show_img">
                        <span :class="item.icon" v-show="!item.show_img"></span>
                    </div>
                    <el-tooltip class="item" :content="item.name" placement="right">
                        <p class="name">{{ item.name }}</p>
                    </el-tooltip>
                </li>
            </ul>
            <el-dialog title="Custom icon" :visible.sync="customVisible" class="custom_dialog" width="1100px">
                <el-tooltip class="item" effect="light" content="help documentation" placement="right">
                    <i class="mdi mdi-help-circle prompt" @click="toHelp"></i>
                </el-tooltip>

                <ul class="demo-image">
                    <h1 v-show="iconsCustom.length > 0">Local icons</h1>
                    <li v-for="(item, ind) in iconsCustom" :key="ind" class="img_text">
                        <el-radio v-model="pic" :label="item" border>
                            <el-image :src="item" fit="contain">
                            </el-image>
                        </el-radio>
                    </li>
                    <el-divider v-show="iconsCustom.length > 0"></el-divider>
                    <h1>HA icons</h1>
                    <li v-for="(item, ind) in icons" :key="ind" class="img_text">
                        <el-radio v-model="icon" :label="'mdi mdi-' + item" border>
                            <i :class="'mdi mdi-' + item" style="font-size:70px;text-align: center;"></i>
                        </el-radio>
                    </li>
                </ul>
                <el-button type="primary" @click="customDialogConfirm()" round>Confirm</el-button>
            </el-dialog>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            token: window.sessionStorage.getItem('ha_token'),
            ha_url: window.sessionStorage.getItem('hass_url'),
            devices: {},
            entities: {},
            iconsCustom: [],
            deviceName: '',
            deviceId: '',
            projects: '',
            projectName: '',
            customVisible: false,
            pic: '',
            selectEntityName: '',
            ws: '',
            icons: '',
            icon: '',
            selectEntityId: '',
            manufacturer: [],
        }
    },
    created() {
        this.initOp()
        this.getEntity()
        this.projects = JSON.parse(this.$store.state.projects)
    },
    computed: {
        scenario() {
            return this.$store.state.scenario
        },
    },
    watch: {
        "$store.state.scenario": function (newVal) {
            this.scenario = newVal
        },
    },
    methods: {
        toHelp() {
            window.open(this.$store.state.helpIcon, '_blank')
        },
        initOp() {
            this.manufacturer = this.$store.state.manufacturer
            this.$store.commit('setStep', '2')
            this.ws = this.$store.state.webSocket
            this.devices = JSON.parse(this.$store.state.devices)
            this.deviceName = this.$store.state.deviceName
            this.deviceId = this.$store.state.deviceId
            this.projectName = this.$store.state.projectName
            this.icons = this.$store.state.icons
            this.projects = JSON.parse(this.$store.state.projects)
        },
        selectEntity(name, id) {
            this.selectEntityName = name
            this.customVisible = true
            this.selectEntityId = id
        },
        customDialogConfirm() {
            if (this.pic == '' && this.icon == '') {
                this.$message({
                    type: 'error',
                    message: 'Please select an icon!'
                });
                return
            }
            if (this.pic != '') {
                // The selected icon is an image.
                this.entities[this.selectEntityName].show_img = true
                this.entities[this.selectEntityName].img = this.pic
                this.devices[this.deviceId][this.selectEntityId].show_img = true
                this.devices[this.deviceId][this.selectEntityId].img = this.pic
            } else if (this.icon != '') {
                // The selected icon is not an image.
                this.entities[this.selectEntityName].show_img = false
                this.entities[this.selectEntityName].icon = this.icon
                this.devices[this.deviceId][this.selectEntityId].show_img = false
                this.devices[this.deviceId][this.selectEntityId].icon = this.icon
            }
            this.$store.commit('setDevices', JSON.stringify(this.devices))
            this.customVisible = false
            this.pic = ''
            this.icon = ''
        },

        dragStart(e, entity) {
            this.$store.commit('setMoveEntityName', entity.name)
            this.$store.commit('setMoveEntityId', entity.id)
            this.$store.commit('setMoveEntityIcon', entity.icon)
            this.$store.commit('setMoveEntityImg', entity.img)
            this.$store.commit('setMoveEntityShow', entity.show_img)
        },
        back() {
            this.$store.commit('setShow', '1')
        },
        getBase64(file) {
            return new Promise((resolve, reject) => {
                const reader = new FileReader()
                let imgResult = ''
                reader.readAsDataURL(file)
                reader.onload = function () {
                    imgResult = reader.result
                }
                reader.onerror = function (error) {
                    reject(error)
                }
                reader.onloadend = function () {
                    resolve(imgResult)
                }
            })
        },
        getEntity() {
            this.$store.commit('setWsId')
            let uid = this.$store.state.wsId
            let get_entity = {
                'id': uid,
                'type': 'config/entity_registry/list',
            }
            let get_www = {
                'id': uid + 1,
                'method': 'get',
                'timeout': null,
                'type': 'supervisor/api',
                'endpoint': '/addons/core_configurator/info'
            }
            this.ws.send(JSON.stringify(get_entity))
            this.ws.send(JSON.stringify(get_www))
            this.$store.commit('setWsId')
            this.ws.onmessage = (e) => {
                this.$store.commit('checkConnection', { param1: e, param2: this })
                let data = JSON.parse(e.data)
                if (data.id == uid) {
                    // Initialize entity
                    let entities = data.result
                    if (!Object.keys(this.entities).length) {
                        for (let i = 0; i < entities.length; i++) {
                            if (this.manufacturer.includes(entities[i].platform) && entities[i].device_id == this.deviceId) {
                                this.entities[entities[i].original_name] = {}
                                this.entities[entities[i].original_name]['name'] = entities[i].original_name
                                this.entities[entities[i].original_name]['id'] = entities[i].entity_id
                                this.entities[entities[i].original_name]['icon'] = 'mdi mdi-home'
                                this.entities[entities[i].original_name]['show_img'] = false
                                this.entities[entities[i].original_name]['img'] = ''
                                if (!this.devices[this.deviceId][entities[i].entity_id]) {
                                    // icons that have not been changed
                                    this.devices[this.deviceId][entities[i].entity_id] = { 'icon': 'mdi mdi-home', 'show_img': false, 'img': '' }
                                } else {
                                    // Retrieve the change in icon status.
                                    this.entities[entities[i].original_name]['icon'] = this.devices[this.deviceId][entities[i].entity_id].icon
                                    this.entities[entities[i].original_name]['show_img'] = this.devices[this.deviceId][entities[i].entity_id].show_img
                                    this.entities[entities[i].original_name]['img'] = this.devices[this.deviceId][entities[i].entity_id].img
                                }
                            }
                        }
                        this.entities = JSON.parse(JSON.stringify(this.entities))
                        this.$store.commit('setDevices', JSON.stringify(this.devices))
                    } else {
                        this.$message({
                            type: 'error',
                            message: 'No entity found!'
                        });
                    }
                } else if (data.id == uid + 1) {
                    // Retrieve icons from the folder
                    let result = data.result
                    let url = this.ha_url + result.ingress_entry + '/api/listdir?path=/config/www'
                    this.$store.state.instance.get(url).then(res => {
                        let data = res.data.content
                        for (let i = 0; i < data.length; i++) {
                            if (data[i].type == 'file') {
                                this.iconsCustom.push(this.ha_url + '/local/' + data[i].name)
                            }
                        }
                    }).catch(err => console.log(err))
                }
            }
        },
    }
}
</script>
<style scoped lang="less">
@import "./styles/entity.less";
</style>