<template>
    <div class="all">
        <div class="header">
            <p>Device List</p>
        </div>
        <div class="footer">
            <ul>
                <li v-for="item in devices" :key="item.id" @click="changeShowComponent(item.id, item.name)">
                    <el-tooltip placement="right" :content="item.name" :enterable="false">
                        <el-button>
                            <p class="first">{{ item.name }}</p>
                            <p class="second">{{ item.location }}</p>
                        </el-button>
                    </el-tooltip>
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            token: window.sessionStorage.getItem('ha_token'),
            ha_url: window.sessionStorage.getItem('hass_url'),
            devices: {},
            devicesName: [],
            ws: null,
            // Save the modified icon information in the device
            storeDevices: {},
            // Device manufacturer
            manufacturer: [],
        }
    },
    created() {
        this.manufacturer = this.$store.state.manufacturer
        this.$store.commit('setStep', '2')
        this.ws = this.$store.state.webSocket
        this.getDevices()
        this.storeDevices = JSON.parse(this.$store.state.devices)
    },
    methods: {
        changeShowComponent(id, name) {
            // Navigate to entity page
            this.$store.commit('setShow', '2')
            this.$store.commit('setDeviceId', id)
            this.$store.commit('setDeviceName', name)
        },
        getDevices() {
            this.$store.commit('setWsId')
            let uid = this.$store.state.wsId
            let get_device = {
                'id': uid,
                'type': 'config/device_registry/list'
            }
            this.ws.send(JSON.stringify(get_device))
            this.$store.commit('setWsId')
            this.ws.onmessage = (e) => {
                this.$store.commit('checkConnection', { param1: e, param2: this })
                let data = JSON.parse(e.data)
                if (data.id == uid) {
                    let devices = data.result
                    for (let i = 0; i < devices.length; i++) {
                        let identifier = devices[i].identifiers[0][0]
                        if (this.manufacturer.includes(identifier)) {
                            this.devicesName.push(devices[i].name)
                            this.devices[devices[i].name] = {}
                            this.devices[devices[i].name]['name'] = devices[i].name
                            this.devices[devices[i].name]['location'] = devices[i].area_id
                            this.devices[devices[i].name]['id'] = devices[i].id
                            if (!this.storeDevices[devices[i].id]) {
                                this.storeDevices[devices[i].id] = {}
                            }
                        }
                    }
                    this.devices = JSON.parse(JSON.stringify(this.devices))
                    this.$store.commit('setDevices', JSON.stringify(this.storeDevices))
                }
            }
        },
    }
}
</script>
<style scoped lang="less">
@import "./styles/device.less";
</style>